.zx-admin-panel{direction:ltr;color:#fff;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;height:40px;position:fixed;top:0;left:0;width:100%;min-width:600px;z-index:99999;background:#2f353a;font-size:13px;font-weight:400;line-height:36px}.zx-admin-panel a{color:#fff;position:relative;transition:all .1s ease-in-out;text-decoration:none}.zx-admin-panel ul,.zx-admin-panel ul li{background:0 0;clear:none;list-style:none;margin:0;position:relative;text-indent:0;z-index:99999;padding:0 10px}.zx-admin-panel li{float:left}.zx-admin-panel li.active div{display:block}.zx-admin-panel li a:hover{color:#00b9eb !important}.zx-admin-panel i{position:relative;float:left;speak:none;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:none !important;margin-right:8px;font-size:16px;line-height:40px}.zx-sub-items div{margin:0;padding:0;box-shadow:0 3px 5px rgba(0,0,0,0.2);background:#32373c;display:none;position:absolute;float:none}.zx-sub-items div ul{float:none;padding:0}.zx-sub-items div ul li{float:none;padding:0 13px}@media (max-width: 600px){.zx-admin-panel{display:none}}@media (min-width: 600px){html{margin-top:32px !important}* html body{margin-top:32px !important}}
